'use strict';

var Reflux = require('reflux');
var CountryStore = require('./countryStore');
var FilterStore = require('./filterStore');


var ProjectsStore = Reflux.createStore({

  init: function() {
    this.data = {
      projects: [],
      filter: null,
    };
    this.listenTo(CountryStore, this.updateCountries);
    this.listenTo(FilterStore, this.updateFilter, this.initFilter);
  },

  updateCountries: function(updatedCountries) {
    var projects = updatedCountries.features.reduce(function(projects, country) {
      return projects.concat(country.properties.projects || []);
    }, []);
    this.data.projects = projects;
    this.emit();
  },

  updateFilter: function(newFilter) {
    this.setFilter(newFilter);
  },

  initFilter: function(initialFilter) {
    this.setFilter(initialFilter, true);
  },

  setFilter: function(newFilter, quiet) {
    this.data.filter = newFilter;
    !quiet && this.emit();
  },

  get: function() {
    var this_ = this,
        filteredProjects = this.data.projects;

    if (this.data.projects.some(function(project) { return Boolean(project.properties); }) &&
        this.data.filter.sectors &&
        this.data.filter.sectors.length > 0) {
      var projectsMatchingSector = this.data.projects.filter(function(project) {
        return project.properties.sectors.some(function(sector) {
          return this_.data.filter.sectors.indexOf(sector) > -1;
        });
      });
      filteredProjects = filteredProjects.filter(function(project) {
        return projectsMatchingSector.indexOf(project) > -1;
      });
    }

    return filteredProjects;
  },

  emit: function() {
    this.trigger(this.get());
  },

  getInitialState: function() {
    return this.get();
  }

});


module.exports = ProjectsStore;
